<template>
  <div class="container">
    <h3>Операции</h3>
  </div>
  <hr>
  <div v-if="!isCategoryExist" class="row">
    <div class="col">
      <p>У вас нет ни одной категории.</p>
      <p>Создайте хотя бы одну
        <router-link to="/categories">категорию</router-link>
      </p>
    </div>
  </div>
  <div v-else-if="!isTransactionExist" class="row">
    <div class="col">
      <p>У вас нет ни одной операции.</p>
      <p>Создайте хотя бы одну операцию.</p>
      <p>Для этого нажмите красную кнопку с "+" в нижнем правом углу экрана.</p>
    </div>
  </div>
  <div v-else class="row">
    <div class="row">
      <div class="col s12 m2 offset-m7">
        <YearSelectComponent/>
      </div>
      <div class="col s12 m3">
        <MonthSelectComponent :show-all="true"/>
      </div>
    </div>
    <div class="row table-scrollable">
      <table class="highlight centered responsive-table">
        <thead>
        <tr>
          <th class="green lighten-5">Дата</th>
          <th class="green lighten-5">Тип</th>
          <th class="green lighten-5">Статус</th>
          <th class="green lighten-5">Счёт</th>
          <th class="green lighten-5">Категория</th>
          <th class="green lighten-5">Сумма</th>
          <th class="green lighten-5 fixed-width">Комментарий</th>
          <th class="green lighten-5">Автор</th>
          <th class="green lighten-5">Действия</th>
        </tr>
        </thead>
        <tbody>
        <tr
          v-for="t in transactionsSortedByDate"
          :key="t.id"
          :class="{'grey-text': t.isForecast}">
          <td>{{ dateFilter(t.date) }}</td>
          <td :class="{ 'red-text': t.type === 'outcome',
          'green-text': t.type === 'income', 'blue-text': t.type === 'transfer' }">
            {{ t.type === 'outcome' ? 'Расход' : t.type === 'income' ? 'Доход' : 'Перевод' }}
          </td>
          <td>
            <i v-if="t.isNotPlanned" class="material-icons red-text">error</i>
            <i v-else-if="t.isForecast" class="material-icons blue-text">help</i>
          </td>
          <td>{{ t.account }}</td>
          <td>{{ t.category }}</td>
          <td>
            {{ currencyFilter(t.amount) }}
          </td>
          <td>{{ t.description }}</td>
          <td>{{ t.author }}</td>
          <td>
            <div class="row mb-0 flex-center">
              <div v-if="!t.readonly" class="col">
                <button
                  :id="t.id"
                  :data-target="t.type === 'transfer' ? 'modalEditTransfer' : 'modalEditTransaction'"
                  class="yellow btn modal-trigger black-text"
                  @click="setTransactionId">
                  <i class="material-icons">edit</i>
                </button>
              </div>
              <div class="col">
                <button
                  :id="t.id"
                  class="red modal-trigger btn"
                  data-target="modalDeleteTransaction"
                  @click="setTransactionId">
                  <i class="material-icons">delete</i>
                </button>
              </div>
            </div>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { useFiltersStore } from '@/stores/filters';
import { useTransactionsStore } from '@/stores/transactions';
import { useStatusStore } from '@/stores/status';
import { storeToRefs } from 'pinia';
import MonthSelectComponent from '@/components/MonthSelectComponent.vue';
import YearSelectComponent from '@/components/YearSelectComponent.vue';

const { currencyFilter, dateFilter } = useFiltersStore();
const { selectedTransactionId, transactionsSortedByDate } = storeToRefs(useTransactionsStore());
const { isCategoryExist, isTransactionExist } = storeToRefs(useStatusStore());

const setTransactionId = (event: any) => {
  let el: HTMLButtonElement;
  if (event.target.tagName === 'BUTTON') {
    el = event.target;
  } else {
    el = event.target.parentElement;
  }
  selectedTransactionId.value = el.id;
};
</script>

<style lang="scss" scoped>
.row.mb-0 {
  margin-bottom: 0;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;

  & .ml-0 {
    margin-left: unset;
  }
}

th.fixed-width {
  width: 300px;

  @media (max-width: 992px) {
    width: auto;
  }
}
</style>